body {
  margin: 0;
  font-family: 'Montserrat', sans-serif !important;
  -webkit-font-smoothing: antialiased;
  font-weight: 400;
  line-height: 1.8em !important;
  font-size: 18px;
  overflow-x: hidden !important;
  width:100vw;


  background: url('bgll.png') no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-color: #ffffff; /* Used if the image is unavailable */

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

a {
  font-weight: 700 !important;
  color: #855147;
  text-decoration: none!important;
  cursor: pointer !important;
}

.a-pink {
  color: #CF9DA0 !important;
}

p {
  text-align: justify;
}

.container {
  max-width: 1170px;
  padding: 0 50px;
}

.text_block {
  margin-bottom: 40px;
  margin-top:40px;
}

.footer {
  position: fixed;
  width: 100%;
  height: 60px; /* Set the fixed height of the footer here */
  line-height: 60px; /* Vertically center the text there */
  background-color: #f5f5f5;
  bottom: 1px;

}

footer a {
  font-size: 20px;
}

footer a:first-child {
  margin-right:40px;
  margin-left:-20px;
}

.btn-primary, .Mui-disabled {
  font-family: 'Montserrat', sans-serif !important;
  font-weight: 800 !important;
  letter-spacing: 2px !important;
  background-color: #CF9DA0 !important;
  border-color: #CF9DA0 !important;
  text-transform: uppercase;
}

.btn-primary:hover {
  background-color: #9b7679 !important;
  border-color: #9b7679!important;
}
.btn-primary:active,.btn-primary:focus {
  background-color: #9b7679!important;
  border-color: #9b7679!important;
}

.Mui-disabled {

  background-color: #9b7679!important;
  border-color: #9b7679!important;
}

.simpleParallax {
  position: fixed;
  z-index: -1;
}

.thumbnail {
  width: 100vw;
}

.makeStyles-root-1 .MuiDialogTitle-root {
  background-color: #9b7679!important;
}

.MuiIconButton-label {
  color: white;
}

.makeStyles-root-1 .MuiDialogContent-root .MuiList-root{
  background: #201f1e !important;
}
